@keyframes wiggle {
    25% {
      transform: translateX(5px) rotate(2deg);
    }
    50% {
      transform: translateX(-5px) rotate(-2deg);
    }
    75% {
      transform: translateX(3px) rotate(1deg);
    }
    100% {
      transform: translateX(0) rotate(0);
    }
  }
  
  .animated-wiggle {
    animation: wiggle 0.5s ease-in-out infinite;
  }
  
